@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #1a1a1c;
  color: #fff;
}

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff;
}

.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

.coin-text {
  margin-bottom: 32px;
  text-align: center;
}

.coin-input {
  padding-left: 16px;
  width: 270px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(
    -225deg,
    #ac32e4 0%,
    #7918f2 48%,
    #4801ff 100%
  );

  color: #e2e2e2;
}

.coin-input::-webkit-input-placeholder {
  color: #e2e2e2;
}

.coin-input:-ms-input-placeholder {
  color: #e2e2e2;
}

.coin-input::-ms-input-placeholder {
  color: #e2e2e2;
}

.coin-input::placeholder {
  color: #e2e2e2;
}
.coin-container {
    display: flex;
    justify-content: center;
    
}
  
.coin-row {
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  height: 80px;
  border-bottom: 1px solid #d7d7d7;
  width: 820px  ; 
}



.coin {
  display: flex;
  align-items: center ;
  width: 200px ;
}

.coin > img {
  width: 30px ;
  margin-right: .5rem ;
}

.coin > h1 {
  font-size: 1rem ;
  margin-right: .5rem ;
}

.coin-data {
  display: flex;

}

.coin-data > p {
  margin: 0 1rem ;
}
  

@media only screen and (max-width: 820px ) {
  .coin-row {
     width: 100% ;
     justify-content: center ;
  }
  .coin-symbol {
    display: none ;
  }
  .coin {
    width: 150px ;
  }
}

@media only screen and (max-width: 740px ) {
  .coin-marketcap {
     display: none;
  }
  .coin-row {
    margin-left: 1.5rem ;
    justify-content: start;
 }
 .coin {
  width: 100px ;
}

 .coin > h1 {
  font-size: .5rem ;
  margin-right: .5rem ;
}
.coin-volume {
  display: none ;
}

  
}
